<template>
  <section
    v-if="Object.keys(pricing).length"
    id="pricing-plan"
  >
    <!-- title text and switch button -->
    <div class="text-center">
      <h1 class="mt-5">
        Hr kan du kpa dina mynt
      </h1>
      <p class="mb-2 pb-75">
        Vid kp av ett strre paket fr du fler mynt fr ett billigare pris, kom igng att brja chatta redan idag!
      </p>
    </div>
    <!--/ title text and switch button -->

    <!-- pricing plan cards -->
    <b-row class="pricing-card">
      <b-col
        offset-sm-2
        sm="10"
        md="12"
        offset-lg="2"
        lg="11"
        class="mx-auto mt-5"
      >
        <b-row>
          <b-col
            v-for="item in pricing.data"
            :key="item.coin"
            md="2"
          >
            <b-card
              align="center"
            >
              <b-img
                src="@/assets/images/illustration/coins2.svg"
                class="mb-2 mt-5 half-width"
                alt="basic svg img"
              />
              <div class="annual-plan">
                <div class="plan-price mt-2">
                  <span class="font-weight-bolder text-primary font-large-1">{{ item.coin }}</span><br><br>
                  <span class="font-medium-3">Mynt</span>
                </div>
                <div class="plan-price mt-4">
                  <span class="font-weight-bolder text-primary font-large-1">{{ item.price }}</span><br><br>
                </div>
              </div>
              <b-button
                v-ripple.400="'rgba(40, 199, 111, 0.15)'"
                block
                class="mt-2"
                variant="outline-success"
                @click="buy(item)"
              >
                Buy
              </b-button>
            </b-card>
          </b-col>
        </b-row>
      </b-col>
    </b-row>
    <!--/ pricing plan cards -->
    <!-- pricing faq -->
    <div class="pricing-faq mt-5 py-5 px-md-5">
      <h2 class="text-center">
        Skert - Tryggt - Smidigt - Diskret
      </h2>
      <p class="text-center mt-2">
        Alla betalningar r helt skrade med https-format och PCI-godknda vletablerade betalningsleverantrer. Inkp av mynt kan gras med VISA, Mastercard och eventuellt olika lokala betalningsmetoder som stndigt lggs till fr att underltta vra kunder. Fr att skicka ett meddelande kostar det 10 mynt. Nr du kper mynt kan du anvnda dessa p alla funktioner och privilegier vi erbjuder p webbplatsen. Alla ytterligare externa tjnster som inte ingr i webbplatsens normala funktioner som erbjuds av oss eller andra samarbetspartners tillhandahlls mot extra kostnader om medlemmen vill anvnda ngon av dessa tjnster. Varje extern tjnst har sina egna villkor och vi rekommenderar att du kontrollerar dessa noggrant innan du gr in i dem. S vlkommen att prova vr fantastiska sida och brja chatta, flrta och dejta redan idag. Vnligen ls allt i mer information p vr sida om villkor.
      </p>
    </div>
    <!--/ pricing faq -->
  </section>
</template>

<script>
import {
  BRow, BCol, BCard, BImg, BButton,
  // BCardText, BListGroup, BListGroupItem, BBadge,
} from 'bootstrap-vue'
// import AppCollapse from '@core/components/app-collapse/AppCollapse.vue'
// import AppCollapseItem from '@core/components/app-collapse/AppCollapseItem.vue'
import Ripple from 'vue-ripple-directive'
import useJwt from '@/auth/jwt/useJwt'
import store from '@/store'
/* eslint-disable global-require */
export default {
  components: {
    // BFormCheckbox,
    BButton,
    // BCardText,
    // BListGroup,
    // BListGroupItem,
    BRow,
    BCol,
    BCard,
    // BBadge,
    BImg,
    // AppCollapseItem,
    // AppCollapse,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      status: 'monthly',
      monthlyPlanShow: true,
      pricing: {},
    }
  },
  created() {
    this.$http.get('/pricing/data').then(res => { this.pricing = res.data })
  },
  methods: {
    tooglePlan() {
      if (this.status === 'monthly') {
        this.monthlyPlanShow = true
      } else {
        this.monthlyPlanShow = false
      }
    },
    buy(item) {
      useJwt.createTransaction(item)
        .then(result => {
          this.$swal({
            title: 'You purchased Successfully!',
            text: 'We updated your coins, you get more coins!',
            icon: 'success',
            customClass: {
              confirmButton: 'btn btn-primary',
            },
            buttonsStyling: false,
          })
          store.commit('appConfig/UPDATE_USERDATA', result.data)
          const userData = result.data
          userData.ability = [
            {
              action: 'manage',
              subject: 'all',
            },
          ]
          localStorage.setItem('userData', JSON.stringify(userData))
        })
        .catch(error => {
          console.log(error)
        })
    },
  },
}
/* eslint-disable global-require */
</script>

